.tile{
    position: relative;
    display: -ms-flexbox;
    display: flex;
    /* max-width: 35rem; */
    height: 9rem;
    width: 100%;
    /* justify-content: center; */
    -ms-flex-align: center;
        align-items: center;
    width: 90%;
    margin: 0 0.9rem;
 
    overflow: hidden;
}
.text{
    position: relative;
    color: white;
    z-index: 100;
    padding-left: 1.2rem;
    font-size: 1.4rem;
    max-width: 8rem;
    /* padding-top: 1.4rem; */
    overflow: hidden;
}
.img{
    position: absolute;
    display: inline-block;
    background-position: center;
    width: 100%;
    /* height: 6rem; */
    background-size: cover;
    background-repeat: no-repeat;
}
@media (min-width: 33rem) {
.tile{
margin: 1.5rem;
}

    
}


