.menu{
    width: 100%;
    position: fixed;
    color: white;
    height: 100vh;
    background-color: black;
    z-index: 1000;
    /* display: none; */
}
.menu ul li{
    text-align: center;
    list-style: none;
}
.menu ul{
    display: -ms-flexbox;
    display: flex;
    width: 100%;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-line-pack: center;
        align-content: center;
    font-size: 1.2rem;
    -ms-flex-pack: center;
        justify-content: center;
    justify-items: center;
margin-top: 6rem;
padding: 0;
}
.menu div{
    position: relative;
}
.menu img{
    top: 1rem;
    width: 2.8rem;
 position: absolute;
right: 1.2rem;
cursor: pointer;
}

@media (min-width: 33rem) {
    .menu{
        display: none;
    }
}


