.header {
  color: white;
  width: 100%;
  background: url("../images/mobile/image-hero.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  height: 32rem;
}
.headerContainer {
  display: grid;
  grid-template-columns: 25% 75%;
  grid-template-rows: 100%;
  -ms-flex-pack: center;
      justify-content: center;
  padding: 0 2rem;
  -ms-flex-align: center;
      align-items: center;
  color: white;
  padding: 1rem;
}
.doingList li {
  list-style: none;
  display: inline-block;
  margin-left: 1rem;
}
.brandName {
  display: inline-block;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}
.brandDoings {
  display: inline-block;
}
.text {
  border: 2px solid white;
  color: white;
  z-index: 100;
  padding: 1rem;
  height: 8rem;
  font-weight: 300;
}
.content {
  margin-top: 5rem;
  padding: 0.7rem;
  overflow: hidden;
  width: 80%;
  height: 20rem;
}
.hamburger {
  display: none;
}
@media (max-width: 31rem) {
  .doingList {
    display: none;
  }
  .hamburger {
    display: inline-block;
    cursor: pointer;
  }
  .headerContainer {
    overflow: hidden;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    padding: 0.8rem 1rem;
  }
}



