.footer{
    background-color: black;
    color: white;
    /* padding: 1.2rem; */
    padding: 1rem 0;
    width: 100%;
}
.list{
    text-align: center;
}
.list li{
    list-style: none;
    margin-top: 1rem;
}
.dark{
    position: absolute;
    height: 5rem;
    background-color: black;
    z-index: 200;
}
.list ul{
    padding: 0;
}
.ul{
display: -ms-flexbox;
display: flex;
-ms-flex-align: center;
    align-items: center;
padding: 0;
-ms-flex-pack: center;
    justify-content: center;

}
.ul li{
    list-style: none;
    display: inline-block;
    margin: 1rem;
}

@media (min-width: 33rem) {
    
.ul{
    width: 100%;
    overflow: hidden;
    -ms-flex-align: start;
        align-items: flex-start;
 margin: 0;
 padding: 0;
 -webkit-column-gap: 2rem;
         column-gap: 2rem;
 margin-top: 2rem;
}
.list ul{
    display: -ms-flexbox;
    display: flex;
    padding: 0;
    -ms-flex-pack: distribute;
        justify-content: space-around;
}



}


